import React, { useState, useEffect, useContext, useRef } from 'react';
import { Link } from 'react-router-dom';
import { CartContext } from '../CartContext';
import 'font-awesome/css/font-awesome.min.css';  // Ya no es necesario, lo eliminaremos si usas Material Icons.

import '../styles/navbar.css';

const Navbar = ({ user, onLogout }) => {
  const [menuVisible, setMenuVisible] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);
  const [animate, setAnimate] = useState(false);
  const badgeRef = useRef();

  // Obtn la cantidad de artculos en el carrito desde el contexto
  const { cartItems } = useContext(CartContext);
  const cartItemCount = cartItems.reduce((acc, item) => acc + item.quantity, 0);

  // Toggle para el men responsive
  const toggleResponsiveMenu = () => {
    setMenuOpen(!menuOpen);
  };

  // Animacin para el contador de artculos en el carrito
  useEffect(() => {
    if (badgeRef.current) {
      badgeRef.current.classList.remove('cart-badge');
      void badgeRef.current.offsetWidth; // Forzar reflow para reiniciar la animacin
      badgeRef.current.classList.add('cart-badge');
    }

    setAnimate(true);
    const timeout = setTimeout(() => setAnimate(false), 400); // Duracin de la animacin

    return () => clearTimeout(timeout);
  }, [cartItemCount]);

  return (
    <nav>
  <h1>NOIR</h1>

  {/* Botn hamburguesa */}
  <div className="hamburger" onClick={toggleResponsiveMenu}>
    <span></span>
    <span></span>
    <span></span>
  </div>

  {/* Men principal */}
  <ul className={menuOpen ? 'nav-links open' : 'nav-links'}>
    <li><Link to="/">INICIO</Link></li>
    <li><Link to="/products">PRODUCTOS</Link></li>

    {/* Men de usuario */}
    {user ? (
      <li className="user-menu">
        <span onClick={() => setMenuVisible(!menuVisible)} className="user-icon">
          <span className="material-icons">person</span>
        </span>
        {menuVisible && (
          <ul className="dropdown">
            <li><Link to="/orders" className="dropdown-item">Mis Compras</Link></li>
            <li><button onClick={onLogout} className="dropdown-item logout-btn">Cerrar Sesin</button></li>
          </ul>
        )}
      </li>
    ) : (
      <>
        <li><Link to="/login">INICIAR SESIN</Link></li>
        <li><Link to="/register">REGISTRARSE</Link></li>
      </>
    )}

    {/* Icono del carrito */}
    <li className="navbar-cart">
    <div className="cart-wrapper">
      <Link to="/checkout" 
        className={`cart-icon ${animate ? 'animate' : ''}`} 
        onClick={() => setMenuOpen(false)}
      >
        <span className="material-icons" style={{ fontSize: 24 }}>
          shopping_cart
        </span>
      </Link>

    {cartItemCount > 0 && (
      <span ref={badgeRef} className={`cart-count ${animate ? 'cart-badge' : ''}`}>
        {cartItemCount}
      </span>
    )}
  </div>
</li>
  </ul>
</nav>


  );
};

export default Navbar;
